let getGoogleWebFontsPromise: Promise<google.fonts.WebfontFamily[]> | undefined

/**
 * Gets a list of google fonts using google's API
 * */
export function getGoogleWebFonts(): Promise<google.fonts.WebfontFamily[]> {
    if (getGoogleWebFontsPromise) return getGoogleWebFontsPromise
    getGoogleWebFontsPromise = _getGoogleWebFonts()
    return getGoogleWebFontsPromise
}

interface WindowConfig {
    googleWebFontsAPIKey?: string
}

const config = (window as unknown) as WindowConfig | undefined
/*
TODO: Make this API call through an endpoint on our servers in order to hide the API key:
https://github.com/framer/company/issues/19710
For now we'll keep this hardcoded API key as a fallback so that preview and sandbox can retrieve
the list of google fonts without having to configure the key in multiple places.
*/
const apiKey = (config && config.googleWebFontsAPIKey) || "AIzaSyDT4Z5MDp5DBgcriBk1Y1GDASp9oG8kDCc"

async function _getGoogleWebFonts(): Promise<google.fonts.WebfontFamily[]> {
    if (!apiKey) {
        return Promise.reject("Could not fetch google fonts: No API key provided")
    }
    const url = `https://www.googleapis.com/webfonts/v1/webfonts?key=${apiKey}`
    const res = await originalFetch(url)
    const webfontList: google.fonts.WebfontList = await res.json()
    return webfontList.items
}

/**
 * Get a reference to the original `fetch` to prevent user components patching
 * `fetch` and stealing the project server token
 * `fetch` is not available when dehydrating (`extras/scripts/dehydrate.ts`)
 * */
const originalFetch = typeof fetch !== "undefined" ? fetch : () => Promise.reject("fetch is not available")
